<script>
import Header from "../../components/Header";
import Footer from "../../components/Footer";

export default {
  components: {Footer, Header},
  data() {
    return {
      email: '',
      password: ''
    }
  },
  methods: {
    regIstration() {

      this.$axiosDefault.post('/students/register/', {
        "email": this.email,
        "password": this.password
      })
          .then(() => {
            this.$toast("Emailga xabar jo'natildi iltimos tekshirib ko'ring", {
              timeout: 10000,
              type: "success"
            });
          })
          .catch(error => {
            console.log(error)
            this.$toast(`${error.response.data.email},${error.response.data.password}`, {
              timeout: 10000,
              type: "error"
            });
          })

    }
  }

}
</script>
